<template>
    <div class="Search-header">
        <div v-title data-title="主页">商品搜索</div>
        <div class="Search-wap clearfix">
            <div class="Search-input-main fl">
                <input type="search" v-model="value" @input="get" placeholder="搜索宝贝、课程、讲师...">
            </div>
            <span @click="remove" class="return-btn fl">取消</span>
        </div>
        <div class="Search-content">
            <div class="hot">
                <div class="title"><span></span>热门搜索</div>
                <ul class="clearfix">
                    <li class="fl" v-for="item in hot">{{item}}</li>
                </ul>
                <div class="def" v-show="hot.legnth <= 0">热门搜索为空!</div>
            </div>
            <div class="hit">
                <div class="title">历史搜索</div>
                <ul class="clearfix">
                    <li class="fl" v-for="item in hit">{{item}}</li>
                </ul>
                <div class="def" v-show="hit.length <= 0">你还没浏览过任何商品哦，快去逛逛吧！</div>
            </div>
            <button class="btn" @click="clear" v-show="!hit.length <= 0">清空历史记录</button>
        </div>
    </div>
</template>
<script>
    import { MessageBox } from 'mint-ui';
    import { Toast } from 'mint-ui';
    export default {
        name: 'search',
        data() {
            return {
                value: null,
                hot:['机顶盒','mini','储值卡','配件','曾光辉《太','《股动钱潮'],
                hit:['机顶盒','曾光辉《太曾光辉《太','储值卡']
            }
        },
        methods: {
            get: function() {
                // this.axios.get('/api/5a1Fazu8AA54nxGko9WTAnF6hhy/su', false)
                //     .then((res) => {
                //         console.info('SuccessRes', res);
                //     }).catch((err) => {
                //         console.info('FailtrueErr', err);
                //     });
            },
            remove:function(){
                this.$router.go(-1);
            },
            clear:function(){
                MessageBox.confirm('确定执行此操作?').then(action => {
                    this.hit = [];
                });
            }
        },
        computed() {

        }
    }
</script>
<style lang="less" scoped>

.Search-header{
    background:#f2f2f2;
    .Search-wap {
        height: .6rem;
        padding: .1rem .2rem;
        background: -webkit-linear-gradient(left, #f97522, #fd5148);
        background: -o-linear-gradient(right, #f97522, #fd5148);
        background: -moz-linear-gradient(right, #f97522, #fd5148);
        background: linear-gradient(to right, #f97522, #fd5148);
        .return-btn {
            width: .8rem;
            height: .6rem;
            text-align: center;
            line-height: .6rem;
            font-size: .28rem;
            color: #fff;
            em{
                position:absolute;
                left:.45rem;
                top:.06rem;
                i{
                    border-right:10px solid #fff;
                    border-top:10px solid transparent;
                    border-bottom:10px solid transparent;
                    position:absolute;
                    right:0;
                    top:0;
                }
                b{
                    border-right:10px solid #0097fa;
                    border-top:10px solid transparent;
                    border-bottom:10px solid transparent;
                    position:absolute;
                    right:-.04rem;
                    top:0;
                }
            }
        }
        .Search-input-main {
            width: 6.3rem;
            height: .6rem;
            border-radius: 100px;
            background: rgba(250,250,250,.5);
            line-height:100%;
            input {
                width: 6.2rem;
                padding-right: .1rem;
                height: 100%;
                border: none;
                border-radius: 100px;
                background: url(../../assets/search.png) no-repeat .2rem .15rem;
                background-size:.26rem .27rem;
                outline: none;
                text-indent: .6rem;
                font-size: .25rem;
            }
            input::-webkit-input-placeholder, textarea::-webkit-input-placeholder { 
                color: #fff; 
            } 
            input:-moz-placeholder, textarea:-moz-placeholder { 
                color: #fff; 
            } 
            input::-moz-placeholder, textarea::-moz-placeholder { 
                color: #fff; 
            } 
            input:-ms-input-placeholder, textarea:-ms-input-placeholder { 
                color: #fff; 
            } 
        }
    }
    .Search-content {
        .hot,
        .hit{
            padding:.14rem .2rem 0;
            .title{
                font-size:.3rem;
                color:#999;
                padding:.3rem 0;
            }
            .def{
                font-size:.3rem;
                color:#999;
                text-align:center;
            }
            li{
                height:.73rem;
                width:1.75rem;
                padding:0 .25rem;
                font-size:.3rem;
                color:#757575;
                line-height:.73rem;
                text-align:center;
                background:#fff;
                border-radius:5px;
                margin-right:.17rem;
                margin-top:.2rem;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
            li:nth-child(3n){
                margin-right:0;
            }
        }
        .hit{
            padding-bottom:.3rem;
        }
        .hot{
            .title{
                padding-left:.4rem;
                position:relative;
                span{
                    width:.25rem;
                    height:.31rem;
                    position:absolute;
                    left:0;
                    top:50%;
                    margin-top:-.155rem;
                    background:url(../../assets/hot.png) no-repeat;
                    background-size:100% 100%;
                }
            }
        }
        .btn{
            width:7.1rem;
            height:.78rem;
            margin:0 auto;
            display:block;
            font-size:.3rem;
            color:#b3b3b3;
            border:1px solid #b3b3b3;
            border-radius:5px;
            background:#fff;
        }
    }
}
</style>


